<template>
  <div class="chart">    
    <Row class="chart-title"><!--  -->
      
    </Row>
    
    <Row><!--  -->
      <Tabs v-model="mode">
        <TabPane label="" :name="dataMode.TEMP.toString()"></TabPane>
        <TabPane label="" :name="dataMode.STRAIN.toString()"></TabPane>
        <TabPane label="" :name="dataMode.FRE.toString()"></TabPane>
        <TabPane label="" :name="dataMode.ONE.toString()"></TabPane>
        <TabPane label="" :name="dataMode.ORIGIN.toString()" ></TabPane>
      </Tabs> 
    </Row>

    <Row>
      <div class="chart-selector">
        <!--  mode  -->
        <template v-if="mode!== dataMode.ONE.toString()">
          <Select v-model="displayTime" style="width:150px" @on-change="selectTime">
            <Option v-for="item in timeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </template>
        <template v-else>
          <div style="display: flex; align-items: center;">
            <Input v-model="oneinputValue" style="width: 150px; margin-right: 10px" />
            <Button @click="oneButtonClick">m</Button>
          </div>
        </template>
      </div>
    </Row>
    
    <Row>
    <div class="chart-wrap">
      <div class="chart-mask" v-if="loading&&trans"><!-- loading -->
        <Spin fix>
          <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
          <div></div>
        </Spin>
      </div>
      <div class="chart-content"><!-- Echarts -->
        <div id="myChart"  class="echartBox" ref="chartRef" :style="{width: '100%', height: '100%'}">
        </div>
      </div>
    </div>
    </Row>

     <div class="chart-scope-title">
      {{zoomData.label}}
    </div>

    <div v-for="(item, index) in zoomData.axisInfo" :key=index style="display: flex; margin: 10px 0 10px 0; align-items: center">
      <!-- xy -->
      <span style="margin: 0 50px 0 30px">{{item.label}}</span>   <!--  -->
      <span style="margin: 0 10px 0 50px"></span>               
      <Input v-model="item.from" style="width: 150px" />          <!-- from -->
      <span style="margin: 0 10px 0px 20px"></span>
      <Input v-model="item.to" style="width: 150px" />            <!-- to -->

    </div>

    <div style="display:flex; flex-direction:row-reverse;margin-top:20px">
    <!--  -->
      <Button style="margin: 0 25px 0 20px" @click="recvZoom"></Button>   <!-- recvZoom  -->
      <Button type="primary" @click="setZoom" ></Button>                  <!-- setZoom  -->
    </div> 
  </div>
</template>

<script>

// @ is an alias to /src

import enums from '@/enum'
import * as api from '@/api/index.js'


export default {
  name: 'chartView',
  data () {
    return {
      loading: false, // 
      trans: false, // 
      mode: enums.dataMode.FRE.toString(),
      oneinputValue : '', // 
      myChart: null,
      displayTime: 0,//
      chartData: [],
      oneDatax: [],
      data3D:[],
      timer: 0, // 
      // interval: 500000, // 
      interval: 600000, //  10
      // startIndex: 0,
      // endIndex: null,

      timeList:[{//
        value: 0,
        label: ''
      }, {
        value: 1,
        label: '09:34',
      }, {
        value: 2,
        label: '10:00',
      }, {
        value: 3,
        label: '10:21',        
      }, {
        value: 4,
        label: '10:56',
      }, {
        value: 5,
        label: '11:15',   
      }],
      zoomData: { // 
          label: "",
          axisInfo: [
            {
              label: " M",
              from: null,
              to: null,
              type: 'x'
            },
            {
              label: "",//
              from: null,
              to: null,
              type: 'y'
            }
          ]
      },
      actualZoom: [ // 
        {
          type: 'x',
          to: null,
          from: null
        },
        {
          type: 'y',
          to: null,
          from: null
        },
        {
          type: 'z',
          to: null,
          from: null
        }
      ],
      zoomFlag: false, // 
      
    }
  },
  created () {    
    switch (this.mode) {
      case enums.dataMode.TEMP.toString():
        this.trans = true
        this.getFreshiftTimeList()
        break;
      case enums.dataMode.STRAIN.toString():
        this.trans = true
        this.getFreshiftTimeList()
        break;
      case enums.dataMode.FRE.toString():
        // this.chartData = this.$store.state.tableData;
        // console.log("this.chartData:",this.chartData)
        this.trans = true
        this.getFreshiftTimeList()
        break;
      case enums.dataMode.ONE.toString():
        this.trans = true
        break;
      case enums.dataMode.ORIGIN.toString():
        break;
      default:
        break;
    }
    
  },
  // this.mode
  mounted () {
    switch (this.mode) {
      case enums.dataMode.TEMP.toString():
        this.getTempData()
        break;
      case enums.dataMode.STRAIN.toString():
        this.getTempData()
        break;
      case enums.dataMode.FRE.toString():
        this.getFreshiftData()
        break;
      case enums.dataMode.ONE.toString():
        // this.chartData = []
        break;
      case enums.dataMode.ORIGIN.toString():
        this.getOriData()
        console.log(this.data3D.length);
        break;
      default:
        break;
    }
     
    
  },
  computed:{
    dataMode(){
      return enums.dataMode
    },
    chartTitle(){
      switch (this.mode) {
        case enums.dataMode.TEMP.toString():
          return ''
        case enums.dataMode.STRAIN.toString():
          return ''
        case enums.dataMode.FRE.toString():
          return ''
        case enums.dataMode.ONE.toString():
          return ''
        case enums.dataMode.ORIGIN.toString():
          return ''
        default:
          return '' // default
      }
    },
    chartYname () {
      switch (this.mode) {
        case enums.dataMode.TEMP.toString():
          return '/'
        case enums.dataMode.STRAIN.toString():
          return ''
        case enums.dataMode.FRE.toString():
          return '/kHz'
        case enums.dataMode.ONE.toString():
          return '/kHz'
        case enums.dataMode.ORIGIN.toString():
          return ''
        default:
          return ''
      }
    },
    chartYunit () {
      switch (this.mode) {
        case enums.dataMode.TEMP.toString():
          return ''
        case enums.dataMode.FRE.toString():
          return 'kHz'
        case enums.dataMode.ONE.toString():
          return 'kHz'
        default:
          return ''
      }
    },
    chartXname () {
      switch (this.mode) {
        case enums.dataMode.TEMP.toString():
          return '/m'
        case enums.dataMode.FRE.toString():
          return '/m'
        case enums.dataMode.ONE.toString():
          return ''
        default:
          return ''
      }
    },
    limit () { // 
      switch (this.mode) {
        case enums.dataMode.TEMP.toString():
          return 39
        case enums.dataMode.STRAIN.toString():
          return 15
        case enums.dataMode.FRE.toString():
          return 29//200
        case enums.dataMode.ONE.toString():
          return 20//200
        default:
          return 0;
      }    
    },
    lineOption () {


      const originalData = this.chartData; //  this.chartData 
      console.log("284:",originalData)
        let startIndex = 0;
        let endIndex = originalData.length - 1;

        //  startIndex  endIndex 
        if (this.startIndex!== undefined) {
            startIndex = Math.max(0, Math.min(this.startIndex, originalData.length - 1));
        }
        if (this.endIndex!== undefined) {
            endIndex = Math.max(0, Math.min(this.endIndex, originalData.length - 1));
        }
        if (startIndex > endIndex) {
            [startIndex, endIndex] = [endIndex, startIndex];
        }

        const pointCount = this.getPointCount();
        const data = this.filterData(originalData, pointCount, startIndex, endIndex);



      return {
        title: {//
          text: this.chartTitle, // 
          left: 'center',
        },
        tooltip: { // 
          trigger: 'axis',  //.axis:
          triggerOn: 'click', //  tooltip
          // formatter: `{b0}m<br />${this.chartYname}{c0}${this.chartYunit}`, //m{Y}{}
          formatter: (params) => {
                        const param = params[0];
                        return `: ${param.data[0]}m<br/>${this.chartYname}: ${param.data[1]}kHz`;
                    },
          extraCssText: 'text-align: left',// CSS
          padding: 10,
        },
        grid: { // 
          left: '3%',
          right: '8%',
          bottom: '10%',
          containLabel: true
        },
        toolbox: { //  
          top: 0,
          right: 100,
          feature: {
            restore: {},  // 
            saveAsImage: {} // 
          },
        },
        xAxis: {
          type: 'category', // 
          name: this.chartXname,
          // data: this.getXaxis(this.chartData),
          data: this.getXaxis(),
          min: 0,
          max: this.chartData.length - 1,
          boundaryGap: false,
          splitLine: {
            show: true,
            lineStyle: {
              color: '#999', // 
              width: 2 // 
            }
          }
        },
        yAxis: {
          name: this.chartYname,
          type: 'value',
          splitLine: {
            show: true,
            lineStyle: {
              color: '#999', // 
              width: 2 // 
            }
          }
        },
        visualMap: {  // ---
          top: 0,
          right: 10,
          pieces: [{
              gt: 0,
              lte: this.limit,
              color: '#2b85e4'
          }, {
            gt: this.limit,
            color: 'red'
          }],
        },
        dataZoom: [{ // 
          type: 'inside', // --inside 
          show: true,  
          xAxisIndex: 0,
          start: (startIndex / (originalData.length - 1)) * 100,
          end: (endIndex / (originalData.length - 1)) * 100,
          // end: 100,

        }, 
        {
          type: 'slider',
          xAxisIndex: 0,
          start: (startIndex / (originalData.length - 1)) * 100,
          end: (endIndex / (originalData.length - 1)) * 100
        },
        {
          type: 'slider', // y
          yAxisIndex: 0,  //y0
        }, 
        {
          type: 'inside', // x
          yAxisIndex: 0,
        }
      ],
        series: [ // 
          {
            name: this.chartYname,
            type:'line',
            stack: '',
            sampling:"lttb",
            data: data,
            symbol:'emptyCircle', // 
            smooth: true,
            showAllSymbol: 'true',//
            symbolSize:5, //
            color: '#2b85e4', 
            markLine: {// 
              symbol:"none",
              silent: true,
              label:{
                position:"start" // start,"middle","end"    
              },
              data: [{
                yAxis: this.limit,
                lineStyle:{ //  2b85e4   FA3934
                  color:"#2b85e4",
                },
              }]
            },
          },
        ],
      }
    },

    lineOption_3D () {
      return {
        tooltip: {},
        title: {//
          text: this.chartTitle, // 
          left: 'center',
        },
        backgroundColor: '#fff',
        toolbox: { //  
          top: 0,
          right: 100,
          feature: {
            dataZoom: {   // 
              yAxisIndex: 0,
              //  xAxis/yAxisx/y false x
              //  3  axisIndex  3 x [0, 3]  axisIndex  0  3 x
              xAxisIndex: 0,
            },
            restore: {},  // 
            saveAsImage: {} // 
          },
        },
        visualMap: {
            show: false,
            dimension: 2,
            min: 0,
            max: 30,
            inRange: {
                color: ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026']
            }
        },
        xAxis3D: {
            type: 'value'
        },
        yAxis3D: {
            type: 'value'
        },
        zAxis3D: {
            type: 'value'
        },
        grid3D: {
            viewControl: {
                projection: 'orthographic'
            }
        },
        series: [{
            // type: 'scatter3D',
            type: 'surface',
            data: this.data3D,
            // lineStyle: {
            //     width: 4
            // }
        }]
      }
    },

  },
  beforeDestroy () {
    this.clearTimer()
  },
  watch: {
    mode (val) { // mode
      // this.clearTimer() //
      console.log('mode',this.mode)
      // let newLineOption = JSON.parse(JSON.stringify(this.lineOption))/**/
      let newLineOption
      let newLineOption_3D
      // let newLineOption_3D = JSON.parse(JSON.stringify(this.lineOption_3D))/**/
      switch (val) {
        case enums.dataMode.TEMP.toString():
          this.trans = true
          // this.getTempHttp(0)  //created
          newLineOption = JSON.parse(JSON.stringify(this.lineOption))
          this.myChart.clear()
          this.myChart.setOption(newLineOption,true)
          this.getTempData()   // 
          this.initZoom()         //
          this.updateChart(this.chartData)      //++

          this.zoomData.label = ""
          this.zoomData.axisInfo.forEach((item) => {
            if (item.type === 'y') {
              item.label = ""
            } // :todo z
          })

          break;
        case enums.dataMode.STRAIN.toString():
          this.trans = true
          newLineOption = JSON.parse(JSON.stringify(this.lineOption))
          this.myChart.clear()
          this.myChart.setOption(newLineOption,true)
          this.getTempData()   // -> getTempHttp(0) -> updataChart
          this.initZoom()         //
          this.updateChart(this.chartData)//
          
          this.zoomData.label = ""
          this.zoomData.axisInfo.forEach((item) => {
            if (item.type === 'y') {
              item.label = ""
            } // :todo z
          })
          
          break; 
        
        case enums.dataMode.FRE.toString():
          this.trans = true
          newLineOption = JSON.parse(JSON.stringify(this.lineOption))
          this.myChart.clear()
          this.myChart.setOption(newLineOption,true)
          this.getFreshiftData()   // -> getTempHttp(0) -> updataChart
          this.initZoom()         //
          this.updateChart(this.chartData)//
          
          this.zoomData.label = ""
          this.zoomData.axisInfo.forEach((item) => {
            if (item.type === 'y') {
              item.label = "Khz"
            } // :todo z
          })
          
          break;
        case enums.dataMode.ONE.toString():
          this.trans = true
          newLineOption = JSON.parse(JSON.stringify(this.lineOption))
          this.myChart.clear()
          // this.myChart.setOption(newLineOption,true)
          // this.getFreshiftData()   // -> getTempHttp(0) -> updataChart
          // this.initZoom()         //
          // this.updateChart(this.chartData)//
          
          this.zoomData.label = ""
          this.zoomData.axisInfo.forEach((item) => {
            if (item.type === 'y') {
              item.label = "Khz"
            } // :todo z
          })
          
          break;
       case enums.dataMode.ORIGIN.toString():
          this.trans = true
          this.getOriData()
          newLineOption_3D = JSON.parse(JSON.stringify(this.lineOption_3D))/**/
          // 3D
          // this.getOriginData()//
          // this.initZoom()//

          console.log('3D',newLineOption_3D)
          this.myChart.clear()
          this.myChart.setOption(newLineOption_3D,true)
          break;
        default:
          break;
      }
      
       //
    }
  },
  methods: {
    initZoom() {
      this.zoomFlag = false
      this.actualZoom = [ 
        {
          type: 'x',
          to: null,
          from: null
        },
        {
          type: 'y',
          to: null,
          from: null
        },
        {
          type: 'z',
          to: null,
          from: null
        }
      ]
    },

    startChart () {
        this.myChart = this.$echarts.init(document.getElementById('myChart'));// 
          // this.myChart.setOption(this.lineOption,window.onresize = myChart.resize)// domecharts
          //setOption:Echarts

        //  dataZoom 
        this.myChart.on('dataZoom', () => {
          const option = this.myChart.getOption();
          const dataZoom = option.dataZoom[0];
          const start = dataZoom.start / 100 * (this.chartData.length - 1);
          const end = dataZoom.end / 100 * (this.chartData.length - 1);
          const ydataZoom = option.dataZoom[3];
          console.log('11569:',option.dataZoom[0]);
          console.log('11569:',option.dataZoom[3]);
          // const ystart = ydataZoom.start / 100 * Math.max(...this.chartData);
          // const yend = ydataZoom.end / 100 * Math.max(...this.chartData);
          const ystart = ydataZoom.startValue;
          const yend = ydataZoom.endValue;
          console.log('aa569:',Math.max(...this.chartData));
          console.log('569:',start, end, ystart, yend);
          this.updateChart(this.chartData, start, end,ystart,yend);
        });

        // 
        window.addEventListener('resize', () => {
          this.myChart.resize();
          this.updateChart(this.chartData);
          console.log('resize!!!')
        });
          
        // 123----
          this.myChart.getZr().on('contextmenu', (params) => {
            // 
            if (params.event && params.event.preventDefault) {
                params.event.preventDefault();
            }
            if (
                this.mode === this.dataMode.TEMP.toString() ||
                this.mode === this.dataMode.STRAIN.toString() ||
                this.mode === this.dataMode.FRE.toString()
              ){
                    // 
                const pointInPixel = [params.offsetX, params.offsetY];
                // 
                const pointInGrid = this.myChart.convertFromPixel('grid', pointInPixel);

                // 
                if (pointInGrid) {
                    const distance = pointInGrid[0]; //  X   
                    console.log('649 X :', distance);
                    this.myChart.clear();
                    this.mode = this.dataMode.ONE.toString(); // 
                    this.oneinputValue = Math.ceil(distance); //  ----
                    this.oneButtonClick(); // 
                }
              }
            
        });
 
        
        this.myChart.setOption(this.lineOption,true);
            
    },
    getFreshiftTimeList () {
      api.getFreshiftTimeList().then((res) => {
        if (res.code === 1) {
          // console.log("res:",res)
          this.timeList = res.data.timeList.map((item) => {//
            return {value: ((new Date(item)).getTime() - 8 * 60 * 60 * 1000)/1000, label: item.toLocaleString()}
            })
          this.timeList.unshift({ // unshift()         
            value: 0,
            label: ''
          })
        } else if(res.code === 0) {
            this.$Message.error(res.message)
        }
      })
    },
    selectTime (target) { // 
      this.clearTimer()//
      if (target === 0) {//
        this.trans = false //
        switch (this.mode) {
          case enums.dataMode.TEMP.toString():
            this.getTempData() //getTempHttpgetTempData
            break;
          case enums.dataMode.STRAIN.toString():
            this.getTempData()
            break;
          case enums.dataMode.FRE.toString():
            this.getFreshiftData()

            break;
          default:
            break;
        }
      } else {
        this.trans = true //
        // target= target
        switch (this.mode) {
          case enums.dataMode.TEMP.toString():
            this.getTempHttp(target)  //
            break;
          case enums.dataMode.STRAIN.toString():
            this.getTempHttp(target)
            // this.getStrainHttp(target)
            break;
          case enums.dataMode.FRE.toString():
            console.log("qita:",target)
            this.getFreshiftHttp(target)
            // this.getStrainHttp(target)
            break;
          default:
            break;
      }
     }
     this.trans = false
    },
    getXaxis () {//x---
      let result = []
      // console.log("val.lengt")
      // console.log(this.chartData.length)
      if (this.mode == enums.dataMode.ONE.toString())
      {
        result = this.oneDatax;
      }else{
          for (let i = 0; i < this.chartData.length; i = i + 1) {
          result.push(i)// 1mx 
        }
      }
      
      return result
    },
    clearTimer () {
      console.log("Timer haven cleared!")
      // console.log(this.timer)
      clearInterval(this.timer) //JavaScriptsetInterval()
    },
    getFreshiftData(){//
      this.clearTimer()
      this.getFreshiftHttp(0)
      console.log("Timer start")
      this.getFreshiftTimeList()
      // var _this = this;  //thisvuevue
      this.timer = setInterval(() => {  // setIntervalclearInterval
        this.trans = false  //{}
        console.log("getFREData0") 
        this.getFreshiftHttp(0)
      }, this.interval) //intervalms(_this.interval)
      // console.log("619;timer:",this.timer)
    },
    oneButtonClick() {
      // 
      console.log('', this.oneinputValue);
      this.clearTimer()
      console.log("Timer start")
      this.getOneHttp(this.oneinputValue)//length
      this.timer = setInterval(() => {  // setIntervalclearInterval
        this.trans = false  //{}
        console.log("getFREData0") 
        this.getOneHttp(this.oneinputValue)
      }, this.interval) //intervalms(_this.interval)

    },
    getTempData(){//
      this.clearTimer()
      this.getTempHttp(0)
      console.log("Timer start")
      this.getFreshiftTimeList()
      // var _this = this;  //thisvuevue
      this.timer = setInterval(() => {  // setIntervalclearInterval
        this.trans = false  //{}
        console.log("getData0") 
        this.getTempHttp(0)
      }, this.interval) //intervalms(_this.interval)
      // console.log("619;timer:",this.timer)
    },
    getFreshiftHttp(time) {//time=0
        console.log("GetFREData--time:",time)
        let arg = {
          "time" : time
        }
        this.loading = true
        api.getFreshiftData(arg).then((res) => {
          if (res.code === 1) {
            this.chartData = res.data.freshift
            console.log("794res:",res)
            // console.log("this.chartData",res)
            this.startChart ()//
            this.loading = false
          } else if(res.code === 0) {
            this.$Message.error(res.message)
          }
        })
       
    },
    getTempHttp(time) {//time=0
        console.log("GetData--")
        let arg = {
          "time" : time
        }
        this.loading = true
        console.log("arg:")
        api.getTempData(arg).then((res) => {
          if (res.code === 1) {
            this.chartData = res.data.temp
            this.startChart ()//
            this.loading = false
          } else if(res.code === 0) {
            this.$Message.error(res.message)
          }
        })
       
    },
    getOneHttp(val) {//time=0
        console.log("789GetData--")
        let arg = {
          "onedata" : val
        }
        this.loading = true
        api.getOneData(arg).then((res) => {
          if (res.code === 1) {
            // console.log(res.data.value)
            // console.log(res.data.time)
            // console.log(res.data)
            this.chartData = res.data.value
            this.oneDatax = res.data.time
            this.startChart ()//
            this.loading = false
          } else if(res.code === 0) {
            this.$Message.error(res.message)
          }
        })
       
    },
    // -------------------------------
    // 
    getPointCount() {
      const screenWidth = window.innerWidth - 540;//
      console.log('713:',screenWidth);
      if (this.chartData.length > screenWidth) {
        return screenWidth;
      } else {
        return this.chartData.length;
      }
    },
    // 
    filterData(originalData, pointCount, startIndex, endIndex) {
      const rangeData = originalData.slice(startIndex, endIndex + 1);
      const interval = Math.ceil(rangeData.length * 2 / pointCount);
      const selectedData = [];

      for (let i = 0; i < rangeData.length; i += interval) {
        const subEndIndex = Math.min(i + interval, rangeData.length);
        const subArray = rangeData.slice(i, subEndIndex);
        let maxValue = -Infinity;
        let minValue = Infinity;
        let maxIndex = -1;
        let minIndex = -1;

        for (let j = 0; j < subArray.length; j++) {
          if (subArray[j] > maxValue) {
            maxValue = subArray[j];
            maxIndex = i + j + startIndex;
          }
          if (subArray[j] < minValue) {
            minValue = subArray[j];
            minIndex = i + j + startIndex;
          }
        }

        if (maxIndex < minIndex) {
          selectedData.push([maxIndex, maxValue]);
          selectedData.push([minIndex, minValue]);
        } else {
          selectedData.push([minIndex, minValue]);
          selectedData.push([maxIndex, maxValue]);
        }
      }

      return selectedData.slice(0, pointCount);
    },
    // !!!!!!!!!!!!!!!!!!
    updateChart(originalData, startIndex = 0, endIndex = null, ystartIndex = 0, yendIndex = null) {
      const pointCount = this.getPointCount();
            console.log('763:',pointCount);
            
            if (endIndex === null) {
                endIndex = originalData.length - 1;
            }
            if (yendIndex === null) {
                yendIndex = Math.max(...originalData);
            }  
            console.log('765:',ystartIndex,yendIndex);           
            const data = this.filterData(originalData, pointCount, startIndex, endIndex);
            console.log('768:',data);
            const option = {
                tooltip: {
                    trigger: 'axis',
                    triggerOn: 'click', //  tooltip
                    axisPointer: {
                        crossStyle: {
                            color: '#999'
                        }
                    },
                    formatter: (params) => {
                        const param = params[0];
                        return `: ${Math.floor(param.data[0])}m<br/>${this.chartYname}: ${param.data[1]}`;
                    },
                },
                xAxis: {
                    type: 'value',
                    min: 0,
                    max: originalData.length - 1,
                    boundaryGap: false,
                    splitLine: {
                    show: true,
                    lineStyle: {
                      color: '#999', // 
                      width: 2 // 
                    }
                  },
                },
                yAxis: {
                    type: 'value',
                    // min: 0,
                    // max: 100,
                    boundaryGap: false,
                    splitLine: {
                    show: true,
                    lineStyle: {
                      color: '#999', // 
                      width: 2 // 
                    }
                  }
                },
                dataZoom: [
                    {
                        type: 'slider',
                        xAxisIndex: 0,
                        start: startIndex / (originalData.length - 1) * 100,
                        end: endIndex / (originalData.length - 1) * 100
                    },
                    {
                        type: 'inside',
                        xAxisIndex: 0,
                        start: startIndex / (originalData.length - 1) * 100,
                        end: endIndex / (originalData.length - 1) * 100
                    },
                    {
                        type: 'slider',
                        yAxisIndex: 0,
                        start: ystartIndex / Math.max(...originalData) * 100,
                        end: yendIndex / Math.max(...originalData) * 100,
                        show: true,
                        realtime: true,
                        
                    },
                    {
                        type: 'inside',
                        yAxisIndex: 0,
                        start: ystartIndex / Math.max(...originalData) * 100,
                        end: yendIndex / Math.max(...originalData) * 100,

                    }
                ],
                series: [
                    {
                        data: data,
                        type: 'line',
                        symbol: 'circle',
                        symbolSize: 6,
                        showSymbol: true,
                        boundaryGap: false
                    }
                ]
            };

            this.myChart.setOption(option);
    
    },
    validateNumber(input) {
          if (input === 0) {
            return true;
        }
            return!isNaN(input) && input!== "" && Boolean(input);
        },
        handleError(message) {
            this.$Message.error(message);
        },
        validateRange(item, maxValue) {
            let isValid = true;
            const from = Number(item.from);
            const to = Number(item.to);

            if (from > maxValue) {
                this.handleError("");
                isValid = false;
            } else if (to > maxValue) {
                this.handleError("");
                item.to = maxValue;
            } else if (from > to) {
                [item.from, item.to] = [item.to, item.from];
            } else if (from < 0 || to < 0) {
                this.handleError("0");
                isValid = false;
            }
            return isValid;
        },
    //
      setZoom() {
            let hasNaN = 0;
            const maxYValue = Math.max(...this.chartData);
            const maxXValue = this.chartData.length;

            this.zoomData.axisInfo.forEach((item) => {
                if (item.type === 'y') {
                    if (!this.validateNumber(item.to)) {
                        hasNaN++;
                        item.to = maxYValue;
                    }
                    if (!this.validateNumber(item.from)) {
                        hasNaN++;
                        item.from = 0;
                    }
                } else if (item.type === 'x') {
                    if (!this.validateNumber(item.to)) {
                        hasNaN++;
                        item.to = maxXValue;
                    }
                    if (!this.validateNumber(item.from)) {
                        hasNaN++;
                        item.from = 0;
                    }
                }
            });

            if (hasNaN > 0) {
                this.handleError("");
            }

            let isValidRange = true;
            this.zoomData.axisInfo.forEach((item) => {
                const maxValue = item.type === 'y'? maxYValue : maxXValue;
                if (!this.validateRange(item, maxValue)) {
                    isValidRange = false;
                }
            });

            if (!isValidRange) {
                return 0;
            }

            this.zoomFlag = true;
            this.zoomData.axisInfo.forEach((item) => {
                const targetType = item.type;
                this.actualZoom.forEach((val) => {
                    if (val.type === targetType) {
                        val.to = Number(item.to);
                        val.from = Number(item.from);
                    }
                });
            });

            console.log('901:',this.actualZoom);
            this.updateChart(this.chartData,this.actualZoom[0].from,this.actualZoom[0].to,this.actualZoom[1].from,this.actualZoom[1].to);
        },
//
    recvZoom() {
      console.log("recvZoom--recover:"+'recover')
      this.initZoom() //
      this.updateChart(this.chartData)  //
      this.zoomData.axisInfo.forEach((item) => {
          item.to = null
          item.from = null
      })
    },
    
    getOriData(){//3D
      // for (var t = 0; t < 25; t += 0.001) {
      //     var x = (1 + 0.25 * Math.cos(75 * t)) * Math.cos(t)
      //     var y = (1 + 0.25 * Math.cos(75 * t)) * Math.sin(t)
      //     var z = t + 2.0 * Math.sin(75 * t)
      //     this.data3D.push([x, y, z])
      //   }
      //   console.log(this.data3D.length);
      // for (var y = -50; y <= 50; y++) {
      //   for (var x = -50; x <= 50; x++) {
      //       var z = x + y;
      //       this.data3D.push([x, y, z]);
      // }
      let datax = []
      let datay = []
      let dataz = []
      for (var i = 0; i < 100; i += 1) {
          for(var t = 0;t < 100; t += 1){
            datax.push(i) 
            datay.push(t)
            dataz.push(Math.sin(t/10)*t/10)
          } 
      }

      for (var m = 0; m < 100; m += 1) {
          for(var n = 0;n < 100; n += 1){
            let num = (n+(m*100))
            this.data3D.push([datax[num], datay[num], dataz[num]])
          } 
      }
      console.log('data3D:',this.data3D)

    }
  },
}

</script>


