<template>
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="120" >
        <FormItem label="" prop="para_name">
            <Input v-model="formValidate.para_name" placeholder=""></Input>
        </FormItem>
          <FormItem label="" prop="para_code">
            <Input v-model="formValidate.para_code" placeholder=",50005001;"></Input>
        </FormItem>
        <FormItem label="" prop="type_name">
            <Select v-model="formValidate.type_name" placeholder="">
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
            </Select>
        </FormItem>
    <FormItem label="" prop="longth">
            <RadioGroup v-model="formValidate.longth">
                <Radio label="8">8</Radio>
                <Radio label="16">16</Radio>
              <Radio label="32">32</Radio>
            </RadioGroup>
        </FormItem>
        <FormItem label="" prop="writeable">
            <RadioGroup v-model="formValidate.writeable">
                <Radio label="1"></Radio>
                <Radio label="0"></Radio>
            </RadioGroup>
        </FormItem>
        <FormItem label="" prop="write_value">
            <Input v-model="formValidate.write_value" placeholder=""></Input>
        </FormItem>       
        <FormItem label="" prop="unit">
            <Input v-model="formValidate.unit" placeholder=""></Input>
        </FormItem>
				<FormItem label="" prop="farther_submenu">
            <Select v-model="formValidate.farther_submenu" placeholder="">
                <Option value="1"></Option>
                <Option value="2"></Option>
                <Option value="3"></Option>
                <Option value="4"></Option>
                <Option value="5">EDFA</Option>
                <Option value="6">SOA</Option>
                <Option value="7"></Option>
                <Option value="8">botda</Option>
            </Select>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit('formValidate')"></Button>
            <Button @click="handleReset('formValidate')" style="margin-left: 8px"></Button>
        </FormItem>
    </Form>
</template>
<script>
import * as api from '@/api/index.js'
    export default {
        data () {
            return {
                formValidate: {
                    para_name: '',
                    para_code: '',
                    type_name: '',
                    longth: '32',
                    writeable:'',
                    write_value :'',
                    unit:'',
                    farther_submenu:'',
                },
              ruleValidate: {
                    para_name: [
                        { required: true,  message: '', trigger: 'blur' }
                    ],
                    para_code: [
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'string', max: 4, message: '4', trigger: 'blur' },
                        { type: 'string', min: 4, message: '4', trigger: 'blur' }
                    ],
                    type_name: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    longth: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    writeable: [
						{ required: true, message: '', trigger: 'change' }
                    ],
                    write_value: [
                        { type: 'string', message: '', trigger: 'blur' }
                    ],
                    unit: [
                        {type: 'string', message: '', trigger: 'blur' }
                    ],
                    farther_submenu: [
                        { required: true, message: '', trigger: 'change' }
                    ]
                }
                
            }
        },
        methods: {
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        
                        console.log(this.formValidate);

                        // ---
                        let arg = {  // Submenu
                            para_name:this.formValidate.para_name,
                            para_code:this.formValidate.para_code,
                            type_name: this.formValidate.type_name,
                            longth: this.formValidate.longth,
                            writable:this.formValidate.writeable,
                            write_value :this.formValidate.write_value,
                            unit:this.formValidate.unit,
                            farther_submenu:this.formValidate.farther_submenu,
                        }
                        api.addParaValue(arg).then((res) => {//api
                            if (res.code === 1) {

                            // 



                            this.$emit('formSubmitted'); // 
                            this.$Message.success('')
                            // code=0
                            
                            // this.updateParaItem // , 
                            } else {
                            this.$Message.error(res.message)
                            // this.spinShow = false
                            // this.$Loading.error()
                            }
                        })

                        
                        
                        

                    } else {
                        this.$Message.error('Fail!');
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
              this.$Message.success('Success!');
            }
        }
    }
</script>
