<template>
  <div class="upload-wrapper">
    <h3 class="title"></h3>
    
    <div class="upload-group" style="display: flex; justify-content: space-between; align-items: center;">
      <div style="display: flex; gap: 20px;">
        <!-- BIN -->
        <div class="upload-item" style="display: flex; gap: 10px;">
          <input
            type="file"
            ref="binInput"
            accept=".bin"
            @change="handleFileSelect('bin')"
            style="display: none"
          >
          <Button type="success" size="small" @click="openFileDialog('bin')" style="height: 36px; padding: 0 16px;">
            <Icon type="ios-archive-outline" /> BIN
          </Button>
        </div>

        <!--  -->
        <div class="upload-item">
          <input
            type="file"
            ref="folderInput"
            webkitdirectory
            directory
            multiple
            accept="*"
            @change="handleFileSelect('folder')"
            style="display: none"
          >
          <Button type="primary" size="small" @click="openFileDialog('folder')" style="height: 36px; padding: 0 16px;">
            <Icon type="ios-folder-open-outline" /> 
          </Button>
        </div>
      </div>
      <Button type="error" size="small" @click="confirmReset" style="height: 36px; padding: 0 16px;">
        <Icon type="ios-refresh" /> 
      </Button>
    </div>

        <!--  -->
    <div v-if="uploadedFiles.length > 0" class="uploaded-files">
      <h4 class="subtitle"></h4>
      <div class="file-list">
        <div v-for="(file, index) in uploadedFiles" :key="index" class="file-item">
          <i class="ivu-icon ivu-icon-document-text" />
          <span class="file-name">
            {{ file.relativePath || file.name }}
          </span>
          <span class="file-size">{{ file.size }}</span>
          <a v-if="file.url" :href="file.url" target="_blank" class="file-action">
            <i class="ivu-icon ivu-icon-download" /> 
          </a>
          <Button type="error" size="small" @click="removeFile(index)" class="file-action">
            <i class="ivu-icon ivu-icon-ios-trash-outline" /> 
          </Button>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import * as api from '@/api/index.js'
export default {
  data() {
    return {
      uploadedFiles: [] // 
    };
  },
  methods: {
    confirmReset() {
  this.$Modal.confirm({
    title: '',
    render: (h) => {
      return h('div', [
        h('p', ''),
        h('div', { style: { margin: '16px 0' } }, [
          h('Checkbox', {
            props: {
              value: this.resetBinChecked //  data 
            },
            on: {
              'on-change': (val) => { this.resetBinChecked = val; }
            },
            style: { display: 'block', marginBottom: '8px' }
          }, 'BIN'),
          h('Checkbox', {
            props: {
              value: this.resetFolderChecked //  data 
            },
            on: {
              'on-change': (val) => { this.resetFolderChecked = val; }
            },
            style: { display: 'block' }
          }, '')
        ]),
        h('p', '')
      ]);
    },
    onOk: () => {
      const selectedOptions = [];
      if (this.resetBinChecked) selectedOptions.push('bin');
      if (this.resetFolderChecked) selectedOptions.push('folder');
      
      if (selectedOptions.length === 0) {
        this.$Message.warning('');
        return Promise.reject();
      }
      
      this.resetToFactorySettings(selectedOptions);
    }
  });
},

resetToFactorySettings(selectedOptions) {
  if (selectedOptions.includes('bin')) {
    // BIN
    console.log('BIN');
  }
  if (selectedOptions.includes('folder')) {
    // 
    console.log('');
  }
  this.$Message.success('');
},
    // 
    openFileDialog(type) {
      if (type === 'bin') this.$refs.binInput.click();
      if (type === 'folder') this.$refs.folderInput.click();
    },

    // 
    handleFileSelect(type) {
  const inputRef = type === 'bin' ? 'binInput' : 'folderInput';
  const fileInput = this.$refs[inputRef];
  
  // 1. 
  if (!fileInput || !fileInput.files || fileInput.files.length === 0) {
    this.$Message.warning('');
    return;
  }

  const files = Array.from(fileInput.files);
  const formData = new FormData();
  const apiMethod = type === 'bin' ? 'uploadBit' : 'uploadSystem';
  const fileKey = type === 'bin' ? 'file' : 'files[]';

  // 2. BIN
  if (type === 'bin') {
    const binFile = files[0];
    if (!binFile || !binFile.name.endsWith('.bin')) {
      this.$Message.error('.bin');
      fileInput.value = '';
      return;
    }
    formData.append(fileKey, binFile);
  } 
  // 3. 
  else {
    files.forEach(file => {
      if (file) { // 
        formData.append(fileKey, file, file.webkitRelativePath || file.name);
      }
    });
  }

  // 4. 
  api[apiMethod](formData)
    .then(res => {
      if (res.code === 1) {
        this.handleUploadSuccess(type, files, res.data);
        fileInput.value = ''; // 
      } else {
        this.$Message.error(res.message || '');
      }
    })
    .catch(error => {
      this.handleUploadError(error, type);
    });
},

// 
handleUploadSuccess(type, files, responseData) {
  const newFiles = files.map(file => ({
    name: file.name || '',
    size: this.formatFileSize(file.size || 0),
    type,
    relativePath: file.webkitRelativePath || '',
    url: responseData?.url || responseData?.urls?.[file.name] || ''
  }));

  this.uploadedFiles = [...this.uploadedFiles, ...newFiles];
  this.$Message.success(`${type === 'bin' ? 'BIN' : ''}`);
},

// 
handleUploadError(error, type) {
  console.error(`${type}:`, error);
  
  let errorMessage = '';
  if (error.response) {
    const status = error.response.status;
    errorMessage = status === 413 ? '' : 
                  status === 404 ? '' : 
                  `(${status})`;
  } else if (error instanceof ProgressEvent) {
    errorMessage = '';
  }

  this.$Message.error(`${type === 'bin' ? 'BIN' : ''}${errorMessage}`);
},
formatFileSize(bytes) {
    if (bytes === 0) return '0 B';
    const k = 1024;
    const sizes = ['B', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  },

    // 
    showStatus(message, type) {
      this.$message({
        message,
        type,
        duration: 1500,
        showClose: true
      });
    }
  }
};
</script>

<style scoped>
.upload-wrapper {
  padding: 20px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.05);
}

.title {
  margin-bottom: 20px;
  font-size: 18px;
  color: #333;
}

.upload-group {
  display: flex;
  gap: 20px;
}

.upload-item {
  display: flex;
  align-items: center;
  gap: 10px;
}
</style>