import Vue from 'vue';
import VueRouter from 'vue-router';
import HomeView from '../views/HomeView.vue'
import LoginView from '../views/loginView.vue';



import HomeRoutes from './home'
import store from '@/store'

Vue.use(VueRouter);

const routes = [
  {
    path: '/home',
    name: 'home',
    component: HomeView,
    children: HomeRoutes,// **
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/admin',
    name: 'admin'
  },
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/offline',
    name: 'offline'
  },
]

const router = new VueRouter({
  mode: 'hash',//historyhash
  base: process.env.BASE_URL,
  routes
});



// 
// to:from:next:true/false
router.beforeEach((to, from, next) => { // **beforeEach
  //  
  let activeMenu;
  HomeRoutes.some((item, i) => {
    if (item.path === to.path) {// **HomeRouter
      activeMenu = i
      return true
    }
  }) && store.commit('setActiveMenu', activeMenu)// **
  next()  //
})

export default router;