<template>
  <div class="table">
    <div class="table-title">
      <!--  -->
      <span class="first-title"></span>
      <span class="second-title">/ {{$route.query.mname}}</span>
      <!-- query Home.vue -->
      <!-- HomegetPareMenugetParaMenu -->
      <!-- <MenuItem v-for="item in paraMenu" :key="item.mid" :name="'3-'+item.mcode" :to="'table?mcode='+item.mcode+'&mid='+item.mid+'&mname='+item.name"> -->
    </div>

    <div class="subMenu-selector" style="width:200px; padding:0 0 20px 20px">
      <!--  -->
      <Select v-model="targetSub" @on-change="selectSubMenu">
        <!-- targetSubsid selectSubMenuParaList-->
        <Option v-for="(item, index) in allSubMenu" :key=index :value=item.sid>{{item.sname}}</Option>
        <!-- allSubMenumidsidsname -->
      </Select>
    </div>
    
    <div class="table-spin">
      <!--  -->
      <div class="table-button">
        <Button v-show="mid === '3'" type="primary" class="button-set" @click="showDataModal"></Button>
          <Modal 
            v-model="dataModalVisible"
            title=""
            width="60%"
          >
              <!--  -->
              <Table
              :columns="columns"
              :data="databaseData"
              border
              :span-method="handleSpan"  
            >
          </Table>

          <!--  -->
          <template #footer>
            <Button type="primary" @click="applyData"></Button>
            <Button @click="closeModal"></Button>
          </template>

          </Modal>


        <Button v-show="mid === '3'" type="primary" class="button-set" @click="backUp"></Button>
<!-- !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! -->
        <Button class="button-set" type="primary" @click="showModal = true"></Button>
        <Modal v-model="showModal" title="" width="600" :footer-hide="true">
            <paraform ref="paraform" @formSubmitted="handleFormSubmitted"></paraform>
        </Modal>

        <Button v-show="mid === '4'" class="button-set"  @click="showAdvancedParams = true" type="primary" style="background-color: #FF7D45; border-color: #FF7D45"></Button>
        <Modal
          v-model="showAdvancedParams"
          title=""
          width="650"
          :mask-closable="false"
          class="advanced-params-modal"
        >
          <div class="advanced-params-container">
            <!-- 1 -->
            <div class="param-group">
              <div class="param-group-header">
                <Icon type="ios-analytics" class="param-group-icon" />
                <h3></h3>
              </div>
              <div class="param-items">
                <div class="param-item">
                  <Checkbox v-model="params.highAccuracy" class="large-checkbox">
                    <span class="param-label"></span>
                    <span class="param-desc">()</span>
                  </Checkbox>
                </div>
                <div class="param-item">
                  <Checkbox v-model="params.enableDebugLog" class="large-checkbox">
                    <span class="param-label"></span>
                    <span class="param-desc">()</span>
                  </Checkbox>
                </div>
              </div>
            </div>

            <!-- 2 -->
            <div class="param-group">
              <div class="param-group-header">
                <Icon type="ios-globe" class="param-group-icon" />
                <h3></h3>
              </div>
              <div class="param-items">
                <div class="param-item">
                  <Checkbox v-model="params.forceIPv4" class="large-checkbox">
                    <span class="param-label">IPv4</span>
                  </Checkbox>
                </div>
                <div class="param-item">
                  <Checkbox v-model="params.bypassProxy" class="large-checkbox">
                    <span class="param-label"></span>
                    <span class="param-desc">()</span>
                  </Checkbox>
                </div>
              </div>
            </div>

            <!--  -->
            <div class="param-group">
              <div class="param-group-header">
                <Icon type="ios-speedometer" class="param-group-icon" />
                <h3></h3>
              </div>
              <div class="param-item-horizontal">
                <span class="param-label">(ms):</span>
                <InputNumber 
                  v-model="params.sampleInterval" 
                  :min="10" 
                  :max="5000"
                  class="styled-input-number"
                />
                <span class="param-unit"></span>
              </div>
            </div>
          </div>

          <div slot="footer" class="modal-footer">
            <Button class="cancel-btn" @click="showAdvancedParams = false"></Button>
            <Button type="primary" class="confirm-btn" @click="saveAdvancedParams"></Button>
          </div>
        </Modal>
        
        <Button class="button-set" @click="setPara" type="primary"></Button>
        <!-- :setPara -->
        <Button class="button-set" @click="queryParaAll"></Button>
        <!-- this.selectSubMenu() -->
        <Button v-show="mid === '4'" class="button-set" @click="startCollecting" type="primary"></Button>
        <!--  -->
      </div>

      <div class="table-content">
        <Table border stripe ref="paraTable" :columns="tcols" :data="tdata" :span-method="handleSpan" :key="DOMkey"></Table>
        <!-- border -->
        <!-- ref -->
        <!-- :columns="tcols" -->
        <!-- :data="tdata"  -->
        <!-- :span-method="handleSpan" / handleSpan -->

        <Spin fix v-if="spinTrans">
            <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
            <div></div>
        </Spin>
      </div>
      
      <Spin size="large" fix v-if="spinShow"></Spin>
    </div>

  </div>
</template>

<script>
import paraform from './paraformView.vue';
import * as api from '@/api/index.js'

// 
const PARAM_TYPE_COLORS = {
  '': 'green',
  '': 'blue',
  '': 'red',

}
export default {
  name: 'paraTable',
  components: {
    paraform
  },
  data () {
    return {
      pollingInterval: null,//
      collectionStatus: 0, // 0- 1- 2-
      showAdvancedParams: false,
      params: {
        highAccuracy: false,
        enableDebugLog: false,
        forceIPv4: true,
        bypassProxy: false,
        sampleInterval: 100
      },
      mid: '',
      mcode: '',
      targetSub: '',
      allSubMenu: [],
      paraList:[],
      typeNumList: [],
      tselection: [],
      inputSetValue: {},
      selectRealData: [],
      // targetPara:[],
      spinShow: false, // /
      spinTrans: false, // sid
      DOMkey:0,
      showModal: false,
      dataModalVisible: false,  // 
      columns: [
        {
          title: '',
          key: 'type',
          align: 'center',
          render: (h, { row }) => {
            return h('Tag', { props: { color: PARAM_TYPE_COLORS[row.type] || 'default' } }, row.type || '')
          },
        },
        {
          title: '',
          key: 'pname',
          align: 'center',
          render: (h, { row }) => {
            return h('strong', row.pname || '')
          }
        },
        {
          title: '',
          key: 'setValue',
          align: 'center',
          render: (h, { row }) => {
            return h('div', [
              h('Icon', { 
                props: { type: 'ios-alert', color: '#ff9900' },
                style: { marginRight: '5px' }
              }),
              h('span', row.setValue || '')
            ])
          }
        }
      ],
      databaseData: [],       // 
    }
  },
  created () {
    // 
    this.mid = this.$route.query.mid
    this.mcode = this.$route.query.mcode
    // 
    this.getAllSubMenu()
  },
  beforeRouteUpdate (to, from, next) {//mid\micode
    console.log(to.query)
    this.mid = to.query.mid   //mid
    this.mcode = to.query.mcode
    // 
    this.getAllSubMenu()
    next()
  },
  computed: {
    tcols () {
      return [ 
        { // 
          title: '',
          key: 'type',
          align: 'center',
          width: 150
        },
        {
          title: '',
          align: 'center',
          key: 'pname'
        },
        {
          title: '',
          align: 'center',
          key: 'readValue'
        },
        {
          title: '',
          align: 'center',
          key: 'setValue',
          render: (h, params) => {
            if (params.row.writable === false) {  //
              return h('span', params.row.setValue) //--writable
            } else {  //
              return h('Input', { // 
                style: {
                  margin: '5px',
                  width: '90px'
                },
                props: {
                  type: 'text', //
                  value: params.row.setValue
                },
                on: { //
                  'on-blur': (event) => { // 
                    let itemInput = JSON.stringify(event.target.value)
                    let flag = 0
                    if(event.target.value =="")
                    {
                        itemInput.replace(/""/g, "null");
                        event.target.value= JSON.parse(itemInput)
                        flag = flag+1
                        console.log("onblur-input:",event.target.value)
                    }
                    
                    params.row.setValue = event.target.value // paraListDOM
                  
                      this.selectRealData.forEach((item) => { //(selectRealDatacollectSelection--)
                        // selectRealData
                        if (item.pid == params.row.pid) {
                          item.setValue = params.row.setValue
                          flag = flag+1
                        }
                        // 
                  this.selectRealData[params.index].setValue = event.target.value
                      })

                    this.selectRealData = this.selectRealData.filter((item)=>item.setValue!=="")

                    if(flag==0){
                      this.selectRealData.push(params.row)
                    }
                    
                    console.log("selectRealData:",this.selectRealData)
                    
                  },
                  input: (event) => {
                    console.log("input1:",event)
                  },
                  'on-focus': () => {
                    // console.log("on-focus1:",event)
                    console.log("on-focus1")
                  }
                },
                nativeOn: {// 
                  'keyup': (event) => { // enter
                    if (event.key.toLowerCase() === 'enter') {//toLowerCase() 
                      params.row.setValue = event.target.value 
                      // this.inputSetValue[params.row.pid.toString()] = params.row.setValue
                    }
                  },
                }
              })
            }
          }
        },
        {
          title: '',
          align: 'center',
          key: 'unit'
        },
        {
          title: '',
          align: 'center',
          key: 'time'
        },
        {
        title: '',
        key: 'action',
        width: 80, // 
        render: (h, params) => {
          return h('Button', {
            props: {
              size: 'small'    // 
            },
            style: {
              backgroundColor: '#FF4D4F', // 
              borderColor: '#FF4D4F',     // 
              color: '#FFFFFF'           // 
            },
            on: {
              click: () => {
                // 
                this.handleRowAction(params.row);
              }
            }
          }, '');
        }
      }
    ]
    },
    tdata () {
      return this.paraList
    },
    formattedData() {
      // 
      return this.databaseData ? JSON.stringify(this.databaseData, null, 2) : '';
    }
  },
  methods: {
    saveAdvancedParams() {
    this.$Message.success('');
    this.showAdvancedParams = false;
    // 
    console.log(':', this.params);
  },
    applyData() {
      // 
      this.$Message.success('');
      // 
      this.dataModalVisible = false;
    },
    closeModal() {
      this.dataModalVisible = false;
    },
    async showDataModal() {
      try {
        const TYPE_ORDER = {
          '': 1,
          '': 2,
          '': 3,
          '': 999 // 
        };
        //  type 
        this.databaseData = this.tdata
          .map((row) => ({
            type: row.type || '',
            pname: row.pname || '',
            setValue: row.setValue !== undefined ? row.setValue : ''
          }))
          .sort((a, b) => {
            const orderA = TYPE_ORDER[a.type] || TYPE_ORDER[''];
            const orderB = TYPE_ORDER[b.type] || TYPE_ORDER[''];
            return orderA - orderB; // 
          });

        this.dataModalVisible = true;
        console.log(this.databaseData);
      } catch (error) {
        console.error(':', error);
        this.$Message.error('');
      }
    },
    backUp(){
      console.log("111")
    },
    // 
    deleteRowData(pcode){
      let arg = {
        pcode: Number(pcode)
      }
      console.log('', pcode);
      api.delParaValue(arg).then((res) => {  //
        if (res.code === 1) {
          this.$Message.success('');
          location.reload();
        } else {
          this.$Message.error(res.message)
        }
      })
    },
    handleRowAction(row) {
      this.$Modal.confirm({
        title: '',
        content: '',
        onOk: () => {
          // 
          this.deleteRowData(row.pcode);
        },
        onCancel: () => {
          // 
          this.$Message.info('');
        }
      });
    },
// 
    handleFormSubmitted () {
            this.showModal = false; // 
            this.selectSubMenu()  //
            location.reload();
        },


    getAllSubMenu () {
      let arg = {
        mid: Number(this.mid)
      }
      api.getSubMenu(arg).then((res) => {//
        if (res.code === 1) {
          this.allSubMenu = res.data.subMenu//
          this.targetSub = this.allSubMenu[0].sid //  
          this.selectSubMenu() // ParaList
        } else {
          this.$Message.error(res.message)
        }
      })
    },
    selectSubMenu () {// **ParaList
      let arg = {
        sid: this.targetSub //
      }
      this.spinTrans = true
      api.getParaList(arg).then((res) => {  //
        if (res.code === 1) {
          this.paraList = res.data.paraList //
          this.paraList.forEach(row => {
            if (row) row.setValue = "";
          });
          // console.log("paraList:",this.paraList)
          let typeObject = {}
          // type
          this.paraList.forEach((item) => {
            if (typeof typeObject[item.type] === 'undefined') {//item.typetype_name
              typeObject[item.type] = [] //
              typeObject[item.type].push(item)
              // push() 
            } else {
              typeObject[item.type].push(item)
            }
          })
          let newParaList = []
          for(let item in typeObject) {
            // 
            newParaList = newParaList.concat(typeObject[item])// concat() 
            this.typeNumList.push(typeObject[item].length) // 
          }
          this.paraList = newParaList //*
          this.spinTrans = false  //*
        } else {
          this.$Message.error(res.message)
          this.spinTrans = false
        }
      })
    },
    // eslint-disable-next-line no-unused-vars
    handleSpan ({ row, column, rowIndex, columnIndex }) {
      //  4 row: ; column: ; rowIndex: ; columnIndex: 
      // rowspancolspan;  rowspan  colspan 
      // 
      if (this.typeNumList.length) {
        // selectSubMenu 0
        let typeNum = this.typeNumList.reduce((a, b) => {return a+b})
        for (let i = 0, index = 0; i <= typeNum - this.typeNumList[this.typeNumList.length-1]; i = i + this.typeNumList[index-1]) {
            if (columnIndex === 0 && rowIndex === i) {
              return {
                rowspan: this.typeNumList[index],
                colspan: 1
              }
            }
            for (let j = i; j < i + this.typeNumList[index]; j ++) {
              if (columnIndex === 0 && rowIndex === j) {
                return [0, 0]
              }
            }
            index ++
        }
      }
    },

    setPara () {

      for (let item of this.selectRealData) {
          if (isNaN(item.setValue) || !(item.setValue)) {//!Boolean(item.setValue),
            this.$Message.error({content:item.pname + '', duration:3})
            return 0
          }
        // longth
          if (Number(item.setValue) >= Math.pow(2, item.longth)) {  // Math.pow(x,y)
            this.$Message.error({content:item.pname + ''+ Math.pow(2, item.longth), duration:3})
          }
      }

      let arg = {  // Submenu
        // mid: Number(this.mid),// **
        // sid: Number(this.targetSub),// **
        paraList: []
      }
      arg.paraList = this.selectRealData.map((item) => {
        return {
          pcode: item.pcode,
          setValue: item.setValue
        }
      })


      console.log("316set",arg)
      api.setParaValue(arg).then((res) => {//api

        console.log(arg)
        if (res.code === 1) {
          this.$Loading.finish()  //
          this.spinShow = false
          this.$Message.success('')
          // 
          this.selectRealData = this.selectRealData.map(item => {
          return {
              ...item,
              setValue: ''  // 
            }
          })
          // code=0
          this.selectSubMenu()  //
          // this.updateParaItem // , 
        } else {
          this.$Message.error(res.message)
          this.spinShow = false
          this.$Loading.error()
        }
      })
    },
    queryParaAll (){
      console.log("updateALL")
      this.selectRealData = this.paraList
      this.selectSubMenu()
      this.selectRealData = []
    },
    startCollecting (){
      let arg = this.paraList.map(row => row.readValue).slice(0, 15);   //botdafarther_submenu_id = 8  

      api.startCollect({arg}).then((res) => {  //
        console.log("",res)
      })
      this.$Message.success("");
      // 2. 
      this.startPolling();
    },
    startPolling() {
      console.log("")
    // 2
    this.pollingInterval = setInterval(async () => {
      try {
        api.checkCollection().then((res) => {  //
            console.log("",res)
            if (res.completed === 0) {
              // 3. 
              clearInterval(this.pollingInterval);
              this.collectionStatus = 2;
              this.$Message.success("");
              console.log(":",res.data)
              // vuex
              // api.getData().then(res => {
              //   console.log(":", res.data)
              //   this.$store.commit('setTableData', res.data) //  Vuex
              // })
            }
          })
      
      } catch (err) {
        console.error(":", err);
        clearInterval(this.pollingInterval);
        this.collectionStatus = 0;
      }
    }, 2000);
  },
  }
}
</script>

<style lang="scss">
.demo-spin-icon-load{
    animation: ani-demo-spin 1s linear infinite;
}
@keyframes ani-demo-spin {
    from { transform: rotate(0deg);}
    50%  { transform: rotate(180deg);}
    to   { transform: rotate(360deg);}
}
.table {
  .table-spin {
    position: relative;
  }
  .table-title {
    padding: 20px 20px 10px 20px;
    text-align: left;
    .first-title {
      font-size: 22px;
      color: #464c5b;
      font-weight: bold;
    }
    .second-title {
      font-size: 16px;
      color: #464c5b;
      padding-left: 10px;
    }
  }
  .table-content {
    padding: 0 20px 0 20px;
    position: relative;
  }
  .table-button {
    display: flex;
    justify-content: flex-end;
    padding: 0 20px 10px 0;
    .button-set {
      margin-right: 10px;
    }
  }
}
/*  */
.advanced-params-modal {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.advanced-params-modal .ivu-modal-header {
  background: linear-gradient(135deg, #f5f7fa 0%, #e4e8eb 100%);
  border-bottom: 1px solid #e8eaec;
  padding: 16px 24px;
}

.advanced-params-modal .ivu-modal-body {
  padding: 20px 24px;
}

/*  */
.param-group {
  padding: 16px;
  margin-bottom: 20px;
  background: #f8fafc;
  border-radius: 6px;
  border-left: 3px solid #2d8cf0;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}

.param-group-header {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.param-group-icon {
  font-size: 18px;
  color: #2d8cf0;
  margin-right: 8px;
}

.param-group h3 {
  font-size: 15px;
  color: #17233d;
  font-weight: 500;
  margin: 0;
}

/*  */
.param-items {
  padding-left: 26px;
}

.param-item {
  margin-bottom: 12px;
}

.param-item-horizontal {
  display: flex;
  align-items: center;
  padding-left: 26px;
  margin-top: 10px;
}

.large-checkbox .ivu-checkbox-inner {
  width: 18px;
  height: 18px;
}

.large-checkbox.ivu-checkbox-checked .ivu-checkbox-inner {
  background-color: #2d8cf0;
  border-color: #2d8cf0;
}

.param-label {
  font-size: 14px;
  color: #515a6e;
  margin-left: 8px;
}

.param-desc {
  font-size: 12px;
  color: #808695;
  margin-left: 5px;
}

/*  */
.styled-input-number {
  margin: 0 10px;
  width: 120px !important;
}

.styled-input-number input {
  text-align: center;
}

.param-unit {
  font-size: 12px;
  color: #808695;
}

/*  */
.modal-footer {
  display: flex;
  justify-content: flex-end;
  padding: 16px 24px;
  border-top: 1px solid #e8eaec;
}

.cancel-btn {
  margin-right: 10px;
  padding: 0 20px;
  height: 36px;
}

.confirm-btn {
  padding: 0 24px;
  height: 36px;
  background: linear-gradient(135deg, #2d8cf0 0%, #1e6de8 100%);
  border: none;
}

.confirm-btn:hover {
  opacity: 0.9;
  box-shadow: 0 2px 8px rgba(45, 140, 240, 0.3);
}
</style>